const locations = JSON.parse(document.getElementById('map').dataset.locations)

mapboxgl.accessToken = 'pk.eyJ1IjoidG9tbXl2ZWU4OCIsImEiOiJjanY3NHhmcWIwYzl2M3lwb2twb3h1MWx4In0.WwMbnS3HfAjjYpPILtiONg'

const map = new mapboxgl.Map({
    container: 'map',
    style: 'mapbox://styles/tommyvee88/cksaovit5av4m17pe7s4es4ix',
    scrollZoom: false
    // center: [-80.128473, 25.781842],
})

const bounds = new mapboxgl.LngLatBounds()

locations.forEach(loc => {
    // Create marker
    const el = document.createElement('div')
    el.className = 'marker'

    // Add marker
    new mapboxgl.Marker({
        element: el,
        anchor: 'bottom'
    }).setLngLat(loc.coordinates).addTo(map)

    // Add popup
    new mapboxgl
        .Popup(
            {
                offset: 30
            }
        )
        .setLngLat(loc.coordinates)
        .setHTML(`<p>Day ${loc.day}: ${loc.description}</p>`)
        .addTo(map)

    // Extend map bounds to include curretn location
    bounds.extend(loc.coordinates)
})

map.fitBounds(bounds, {
    padding: {
        top: 200,
        bottom: 200,
        left: 100,
        right: 100
    }
})


